import React from "react";
import {Icon} from "asset/js/icon";
import classNames from "classnames";
import {Link} from "react-router-dom";
import {nanoid} from "nanoid";

import {getAuthToken, getAuthTrainerId} from 'Util/Authentication';

class Aside extends React.Component {
	constructor(props) {
		super(props);
		this.state={
			centerName : 'bellgym',
			userImage: {
				imageSrc:  'data:image/png;base64,' ,
				setImageSrc: '',
			},
			manageList: [
				{
					title:'스케줄', icon: <Icon.ic24Schedule/>, router: '/schedule/trainer', active: false,
					subMenu: [
						{title:'트레이너', router: '/schedule/trainer', active: false, sub: 'trainer'},
						{title:'내 회원', router: '/schedule/member', active: false, sub: 'member'}
					]
				},
				{title:'회원 관리', icon: <Icon.ic24MemberManage/>, router: '/manage', active: true},
			],
		};

		this.onCheckRouter();
	}

	componentDidMount() {

	}

	onCheckRouter = () => {
		const currentPage = this.props.link;
		this.state.manageList.map((value, index) => {
				if(value.router.match(currentPage)) {
					return value.active = true;
				} else {
					return value.active = false;
				}
			}
		)
	};

	encodeFileToBase64 = (fileBlob) => {
		const reader = new FileReader();
		reader.readAsDataURL(fileBlob);
		console.log(fileBlob);
		if((fileBlob.type).includes('image/'))
			return new Promise((resolve) => {
				reader.onload = () => {
					this.setState({
						userImage: {
							setImageSrc: reader.result
						},
					});
					resolve();
				};
			});
		else {
			alert('이미지 파일이 아닙니다');
		}
	};

	render() {
		const {centerName, userImage, manageList} = this.state;
		const {personalType} = this.props;
		return (
			<div className={'aside'}>
				<div className={'aside_header'}>
					<h1 className={'company'}>
						{ <Icon.logoSmallGo/>}
						{ <Icon.logoSmallGym/>}
						{ <Icon.logoSmallAdmin className={'logo_admin'} />}
					</h1>
					<div className={'profile'}>
						<div className="thumb_area">
							<div className={'thumbnail'}>
								<img src={ userImage.setImageSrc !== '' ? userImage.setImageSrc : userImage.imageSrc} alt=""/>
							</div>

							<input type="file" id={'userProfile'} className={'input_file'} onChange={(e) => { this.encodeFileToBase64(e.target.files[0]);}} />
							<label htmlFor="userProfile" className={'label_file'}> <Icon.ic12Pencil/> </label>
						</div>

						<div className={'information'}>
							<div  className={classNames('name')}>
								{getAuthTrainerId() || '로그인 해주세요.'}
							</div>
							<p className={'description'}>{getAuthTrainerId() || '로그인 해주세요.'}</p>
							<button type={'button'} className={'btn_setting'}>
								<Icon.ic14Setting/> 계정설정
							</button>
						</div>
					</div>
				</div>
				<div className={'aside_content'}>
					<ul className={'menage_list'}>
						{manageList.map((value, index) =>
							<li key={`manage-${value.router}`} className={'item'}>
								<Link to={value.router}  className={classNames('link', {'active' : value.active} )}>
									{value.icon}
									{value.title}
								</Link>
								{/*<span className={'link'}>*/}
									{/*{value.icon}*/}
									{/*{value.title}*/}
								{/*</span>*/}

								{ value.subMenu &&
									<div className={'sub_area'}>
										{value.subMenu.map((value, index) =>
											<Link to={value.router} key={nanoid()} className={classNames('sub_link', {'active' : value.sub === personalType} )}>
												{value.title}
											</Link>
										)}
									</div>
								}
							</li>
						)}
					</ul>
				</div>
			</div>
		);
	}
}

export default Aside;